<template>
	<view class="main">
		<view class="item" v-for="item in dataList">
			<p class="name">{{item.name}}</p>
			<view class="cont" v-if="item.device_have===1">
				<div :class="item.level_status===2?'red':''" class="item-detail">
					水位<span :class="item.level_status===1?'yellow':''">{{item.level}}M</span>
				</div>
				<div  class="item-detail" :class="item.speed_status===2?'red':''">
					流量<span :class="item.speed_status===1?'yellow':''">{{item.speed}}m³/s</span>
				</div>
				<div  class="item-detail" :class="item.rainfall_status===2?'red':''">
					雨量<span :class="item.rainfall_status===1?'yellow':''">{{item.rainfall}}mm</span>
				</div>
			</view>
		</view>
		<view class="null-tip" v-if="dataList.length===0" >暂无数据</view>
	</view>
</template>

<script>
	import {getEnvData} from "@/utils/api/online"
	export default {
		data() {
			return {
				dataList:[]
			}
		},
		onLoad() {
			this.getEnvData()
		},
		methods: {
			async getEnvData(params){
				let res = await getEnvData(params)
				if(res.errno === 0){
					this.dataList = res.data
				}
			},
		}
	}
</script>

<style scoped lang="scss">
	.main{
		display: flex;
		align-items: center;
		flex-direction: column;
		.item{
			background: #FFFFFF;
			border-radius: 8px;
			padding: 18rpx 42rpx;
			margin-bottom: 20rpx;
			width: 710rpx;
			font-size: 28rpx;
			color: #757575;;
			.name{
				font-size: 38rpx;
				color: #333333;
				font-weight: 600;
				text-align: left;
				margin-bottom: 20rpx;
			}
			.cont{
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				font-size: 24rpx;
				line-height: 1.8;
				span{
					margin-right: 70rpx;
					margin-left: 20rpx;
					font-size: 52rpx;
				}

			}
		}
	}
	.null-tip{
		margin-top: 80rpx;
		font-size: 32rpx;
	}
</style>
